import React from "react";
import ava from "../Header/img/ava.jpg";
import s from "./NavAvaAndInfo.module.css";

const NavAvaAndInfo = () => {
  return (
    <div className={s.info}>
      <img src={ava} alt="avatar" />
      <div>
        <h3>Kraichyk Liubomyr</h3>
        <ul>
          <li>Front-end developer</li>
          <li>Age : 25</li>
          <li>Phone : +380-63-591-7759</li>
          <li>Ukraine,Lviv</li>
        </ul>
      </div>
    </div>
  );
};

export default NavAvaAndInfo;
